.experience-main-div {
    display: flex;
    overflow: hidden;
  }

.experience-main {
    width: 90%;
    padding: 20px 10px;
    margin: 0px auto;
}

.experience-text {
    display: flex;
    justify-content: center;
    margin-top: 80px;
    font-size: 70px;
    line-height: 1.1;
    font-family: "cera-bold";
}

.experience-sub-text {
    font-size: 30px;    
    font-family: "cera-medium";  
    text-align: center;
}

.experience-text-p {
    font-size: 40px;
    line-height: 40px;
    margin-right: 40px;
    font-family: "cera-regular";
}

.experience-card {
    border: 1px solid transparent;
    border-radius: 2rem;
    /* border-color: blue; */
    /* background: #fabe2c;     */
    padding: 1.2rem;
    line-height: 1.1;   
    max-width: auto;    
    margin: 0px auto;
}

.experience-card-title {    
    /* border-radius: 2rem; */
    /* border-color: blue; */
    /* background: #fabe2c;     */
    padding: 1.2rem;
    line-height: 1.1;   
    max-width: auto;    
    margin: 0px auto;
}

.logo_img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 40%;
}

.experience-card-body {

}

/* Media Query */
@media (max-width: 1380px) {
    .experience-text {
        font-size: 50px;
    }

    .experience-text-p {
        font-size: 20px;
    }
}

@media (max-width: 768px) {
    .experience-text-p {
        font-size: 16px;
    }
}